import React      from 'react';
import Scrollchor from 'react-scrollchor';
import Radium     from 'radium';
import {Link}     from 'react-router-dom';


import BuyButton  from './BuyButton';

class Nav extends React.Component {
  render() {
    const {links} = this.props;
    return (
      <div style={[styles.container, {position: 'fixed'}]}>
        <div style={styles.inner}>
          <Scrollchor style={styles.logoContainer} to="#">
            <img style={styles.logo} src="img/arroe-logo.svg" />
          </Scrollchor>
          <div style={styles.linkContainer}>
            {
              !!links ? Object.keys(links).map(name => (

                <Link key={name} to={links[name]} style={styles.link}>
                {name}
                </Link>
              )) : null
            }
          </div>
          <div style={styles.orderBtnContainer}>
            <BuyButton link={this.props.buyLink}/>
          </div>
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    backgroundColor: '#1e1e1e',
    height: '80px',
    width: '100%',
    zIndex: 1000,
    position: 'fixed'
  },
  inner: {
    display: 'flex',
    height: '100%',
    flexDirection: 'row',
    justifyContent: 'center',
    margin: 'auto',
    maxWidth: '1000px',
    padding: '0px 20px'
  },
  logoContainer: {
    width: '111px',
    flexGrow: 0,
    display: 'flex',
    justifyContent: 'center'
  },
  linkContainer: {
    flexGrow: 1,
    display: 'flex',
    justifyContent: 'center',

  },
  orderBtnContainer: {
    color: 'White',
    width: '120px',
    flexGrow: 0,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    '@media (max-width: 600px)': {
      display: 'none'
    },
    fontSize: 16,
    textAlign: 'right'
  },
  logo: {
    width: '100%'
  },
  link: {
    color: 'white',
    textTransform: 'uppercase',
    display: 'flex',
    textAlign: 'center',
    flexDirection: 'column',
    justifyContent: 'center',
    margin: '0px 10px',
    textDecoration: 'none',
    fontSize: 14,
    fontWeight: 600,
    letterSpacing: '1.75px'
  },
  buy: {
    color: 'white'
  }
};

export default Radium(Nav);



