import React    from 'react';
import Radium   from 'radium';
import ReactSVG from 'react-svg'

class Features extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selected: '',
    };
  }
  render() {
    const {sections} = this.props;
    const {selected} = this.state;
    return (
      <div style={styles.container}>
        <div style={styles.buttons}>
          {
            !!sections ? Object.keys(sections).map((name) => (
              <div
                key={name}
                style={[styles.button, name === selected ? styles.selected : null]}
                onClick={() => this.setState({selected: name})}>
                <ReactSVG
                  path={sections[name].icon + '.svg'} style={selected === name ? styles.invert : styles.img} />
                <p style={styles.name}>{name}</p>
                {
                  sections[name].text.map((line, k) => (
                    <p key={k} style={styles.text}>{line}</p>
                  ))
                }
              </div>
            )) : null
          }
        </div>
        <div style={styles.details}>
          {
            !!selected ? sections[selected].text.map((line, k) => (
              <p key={k}>{line}</p>
            )) : 'Click an icon above to learn more'
          }
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    maxWidth: '760px',
    margin: 'auto'
  },
  text: {
    fontSize: '16px',
    maxWidth: '500px',
    margin: 'auto'
  },
  buttons: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    '@media (max-width: 600px)': {
      display: 'block'
    }
  },
  button: {
    display: 'flex',
    flexDirection: 'column',
    textAlign: 'center',
    cursor: 'pointer',
    '@media (max-width: 600px)': {
      cursor: 'auto'
    },
    ":hover": {
      '@media (min-width: 601px)': {
        borderBottom: '4px solid black',
        marginBottom: '-4px'
      }
    }
  },
  selected: {
    '@media (min-width: 601px)': {
      borderBottom: '4px solid black',
      marginBottom: '-4px'
    }
  },
  img: {
    border: '3px solid #1e1e1e',
    borderRadius: '50%',
    boxSizing: 'border-box',
    margin: '20px 20px 0px 20px'
  },
  name: {
    fontSize: '16px',
    textTransform: 'uppercase',
    marginTop: '12px',
    marginBottom: '0px',
    fontWeight: 600,
    letterSpecing: '0.4px',
    padding: '0% 20%'
  },
  text: {
    fontSize: '14px',
    display: 'none',
    '@media (max-width: 600px)': {
      display: 'block'
    },
    margin: '0px',
    padding: '15px'
  },
  details: {
    '@media (max-width: 600px)': {
      display: 'none'
    },
    fontSize: '16px',
    padding: '20px 0px',
    minHeight: '40px'
  },
  invert: {
    filter: 'invert(100%)',
    boxSizing: 'border-box',
    margin: '20px 20px 0px 20px'
  }
};

export default Radium(Features);



