import React      from 'react';
import Scrollchor from 'react-scrollchor';
import Radium     from 'radium';
import {Link}     from 'react-router-dom';

class Footer extends React.Component {
  render() {
    const {links} = this.props;

    return (
      <div style={styles.container}>
        <div style={styles.links}>
          {
            !!links ? Object.keys(links).map(network => (
              <a key={network} href={links[network]} style={styles.social}>
                <img src={"/img/" + network + ".svg" } style={styles.icon}/>
              </a>
            )) : null
          }
        </div>
        <p style={styles.smallText}>&copy; {new Date().getYear() + 1900} Arroe Limited. All rights reserved</p>
        <Link style={styles.smallText} to="/privacy">Privacy Policy</Link>
      </div>
    );
  }
}

const styles = {
  container: {
    backgroundColor: '#1e1e1e',
    width: '100%',
    textAlign: 'center',
    padding: 50,
    boxSizing: 'border-box'
  },
  social: {
    height: '20px',
    width: '20px',
    margin: '0px 5px',
    display: 'inline-block'
  },
  icon: {
    height: '100%'
  },
  smallText: {
    margin: 4,
    fontSize: 14,
    color: 'white',
    display: 'block'
  }
};

export default Radium(Footer);



