import React  from 'react';
import Radium from 'radium';

class InTheBox_gr extends React.Component {
  render() {
    var {src} = this.props;
    return (
      <div style={styles.container}>
        <div style={styles.panel}>
          <img src={src} style={styles.img}/>
        </div>
        <div style={styles.panel}>
          <div style={styles.left}>
            <div style={styles.textBox}>
              <p style={styles.content}>LAER mit einer 20,000mAh (74Wh) Batteriekarte</p>
              <p style={styles.content}>USB-C PD 3.0 60W Ladegert und 2m USBC PD 3.0 Ladekabel</p>
            <p style={styles.content}>Laptop-Adapter (USB-C PD, Apple MagSafe 2, Microsoft Surface, 8 Laptop-Adapter PC) und 2m Verlngerungskabele</p>
            <p style={styles.content}>Internationales Adapterstecker-Set: Typ A (US), Typ C (Europa), Typ G (UK), Typ I (Australien)</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    width: '100%',
    display: 'flex',
    '@media (max-width: 600px)': {
      display: 'block'
    }
  },
  left: {
    order: 1,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end'
  },
  right: {
    order: 2,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start'
  },
  panel: {
    width: '100%',
    margin: 'auto',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  img: {
    alignSelf: 'center',
    justifyContent: 'center',
    height: '100%',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
      height: 'auto',
    }
  },
  textBox: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    textAlign: 'left',
    width: '100%',
    boxSizing: 'border-box',
    padding: '40px 20%',
    '@media (max-width: 600px)': {
      paddingLeft : '20%',
      paddingRight : '20%'
    }
  },
  title: {
    fontSize: '20px',
    // textTransform: 'uppercase',
    margin: '0px',
    fontWeight: 'bold'
  },
  content: {
    fontSize: '14px'
  }
};

export default Radium(InTheBox_gr);



