import React from 'react';

import {
  Switch,
  Route,
  Link,
  Redirect
}                from 'react-router-dom';

import {
  Home,
  NotFound,
  Privacy,
  Product,
  About,
  Purchase,
  Purchase_fr,
  Purchase_gr
}                from './pages';

export default (config) => (
  <Switch>
    <Route exact path="/">
      <Home config={config} />
    </Route>
    <Route exact path="/privacy">
      <Privacy config={config} />
    </Route>
    <Route exact path="/product">
      <Product config={config} />
    </Route>
    <Route exact path="/about">
      <About config={config} />
    </Route>
    <Route exact path="/purchase">
      <Purchase config={config} />
    </Route>
    <Route exact path="/purchase_fr">
      <Purchase_fr config={config} />
    </Route>
    <Route exact path="/purchase_gr">
      <Purchase_gr config={config} />
    </Route>
    <Route exact path="/404">
      <NotFound config={config} />
    </Route>
    <Route>
      <Redirect config={config}  to="/404" />
    </Route>
  </Switch>
);



