import React  from 'react';
import Radium from 'radium';

class InTheBox_fr extends React.Component {
  render() {
    var {src} = this.props;
    return (
      <div style={styles.container}>
        <div style={styles.panel}>
          <img src={src} style={styles.img}/>
        </div>
        <div style={styles.panel}>
          <div style={styles.left}>
            <div style={styles.textBox}>
              <p style={styles.content}>LAER avec batterie de 20,000 mAh (74 Wh)</p>
              <p style={styles.content}>Chargeur USB-C PD 3.0 60 W et cable USBC PD 3.0 de 2 m</p>
            <p style={styles.content}>Jeu de cbles et connecteurs (USB-C PD, Apple MagSafe 2, Microsoft Surface, 8 adaptateurs PC, et cable dextension de 2 m)</p>
            <p style={styles.content}>Jeu de prise internationales : USA, Europe, Royaume-uni, Australie</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    width: '100%',
    display: 'flex',
    '@media (max-width: 600px)': {
      display: 'block'
    }
  },
  left: {
    order: 1,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end'
  },
  right: {
    order: 2,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start'
  },
  panel: {
    width: '100%',
    margin: 'auto',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  img: {
    alignSelf: 'center',
    justifyContent: 'center',
    height: '100%',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
      height: 'auto',
    }
  },
  textBox: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    textAlign: 'left',
    width: '100%',
    boxSizing: 'border-box',
    padding: '40px 20%',
    '@media (max-width: 600px)': {
      paddingLeft : '20%',
      paddingRight : '20%'
    }
  },
  title: {
    fontSize: '20px',
    // textTransform: 'uppercase',
    margin: '0px',
    fontWeight: 'bold'
  },
  content: {
    fontSize: '14px'
  }
};

export default Radium(InTheBox_fr);



