import React         from 'react';
import Radium        from 'radium';

import {
  Nav,
  Footer,
  HeroVideo,
  HeroImage,
  Title,
  Text,
  Section,
  ImageSplit,
  ImageSplitPro,
  Features,
  People,
  DeviceLookup,
  DetailsInput,
  BuyButton,
  YouTube
}            from '../components';

class About extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        atTop: true
    }
  }
  componentDidMount() {
      window.addEventListener('scroll', this.handleScroll.bind(this));
  }
  componentWillUnmount() {
      window.removeEventListener('scroll', this.handleScroll.bind(this));
  }
  handleScroll(event) {
      let scrollTop = event.srcElement.body.scrollTop;
      if (scrollTop < 10 != this.state.atTop) {
        /* Only make call to setstate when value is different */
        this.setState({
          atTop: scrollTop < 10
        });
      }
  }
  render() {
    const {atTop} = this.state;
    const {config} = this.props;

    const navLinks = {
      Home: "/",
      Product:   "/product",
      about:    "/about"
    };

    return (
      <div>
        <Nav links={navLinks} buyLink={config.links.buy}/>
        <Section style={styles.dark} scrollName={navLinks.design}>
          {
            Object.keys(config.sections.aboutSplit).map((title, k) => {
              let dir = k % 2 === 0 ? 'left' : 'right';
              let {text, src} = config.sections.aboutSplit[title];
              return (
                <ImageSplit key={k} title={title} direction={dir} text={text} src={src} />
              )
            })
          }
        </Section>
        <Section scrollName={navLinks.about}>
          <Title>The Team</Title>
          <Text style={styles.justify}>{config.sections.about}</Text>
          <People people={config.people} />
        </Section>
        <Section style={styles.dark} scrollName={navLinks.support}>
          <Title>Support</Title>
          <Text>{config.sections.support}</Text>
          <DetailsInput />
          <Text style={styles.padbottom}>
            Want to contact us by email? Email us at <a style={styles.link} href="mailto:support@arroe.co">support@arroe.co</a>
          </Text>
          <br />
        </Section>
        <Section scrollName="notify">
          <Title>Get Notified</Title>
          <Text>Enter your name and email below and well let you know when were launching</Text>
          <DetailsInput signup={true} />
        </Section>
        <Footer links={config.media} />
        <div style={[styles.buybanner, {display: atTop ? 'none' : 'block'}]}>
          <BuyButton link={config.links.buy} />
        </div>
      </div>
    );
  }
}

const styles = {
  features: {
    backgroundImage: 'url(img/hero-image-min.jpg)',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    height: '100vh'
  },
  playbutton: {
    width: '100px'
  },
  dark: {
    backgroundColor: '#1e1e1e',
    color: 'white'
  },
  featuresvideo: {
    width: '760px',
    height: '400px',
    margin: '20px auto 40px',
    maxWidth: '100%'
  },
  buybanner: {
    position: 'fixed',
    bottom: '0',
    width: '100%',
    backgroundColor: '#1e1e1e',
    textAlign: 'center',
    color: 'white',
    '@media (min-width: 601px)': {
      display: 'none'
    },
    padding: '10px',
    boxSizing: 'border-box'
  },
  link: {
    color: 'white'
  },
  justify: {
    textAlign: 'justify'
  },
  container: {
    height: '100vh',
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
    position: 'relative',
    zIndex: 0,
    backgroundImage: 'url(img/Sleeve-min.jpg)',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    paddingTop: '80px',
    boxSizing: 'border-box'
  },
  wrapper: {
    margin: '0 auto',
    position: 'relative'
  },
  panelLeft: {
    order: 1,
    width: '50vh',
    maxWidth: '50vh',
    backgroundColor: 'black',
    float: 'left',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  panelRight: {
    order: 2,
    width: '50%',
    float: 'right',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  innerPanel: {
    width: '50%',
    float: 'right',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  innerPanelL: {
    width: '50%',
    float: 'left',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  "spinner": {
    "width": "50px",
    "height": "50px",
    "borderRadius": "50%",
    "border": "4px solid white",
    "margin": "10px",
    "display": "inline-block"
  },
  "spinner2": {
    "float": "left",
    "width": "50px",
    "height": "50px",
    "background": "transparent",
    "borderRadius": "50%",
    "border": "4px solid #1e1e1e",
    "marginLeft": "100px",
    "display": "inline-block",
    '@media (max-width: 600px)': {
        "width": "30px",
        "height": "30px",
        display: 'block',
    }
  },
  "infoHolder": {
    "marginTop": "-13px",
    "textAlign": "left",
    "marginLeft": "20px",
    "marginRight": "20px",
    "float": "left",
    "display": "inline-block",
    '@media (max-width: 600px)': {
        marginTop : '-5px',
        display: 'block'
    }
  },
  "techBox": {
    "width": "100%",
    height: "15vh",
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }

  },
  "spinnerText": {
    "position": "relative",
    "top": "-3px"
  },
  "smallText": {
    "marginTop": "-20px",
    "fontSize" : "12px"
  }
}

export default Radium(About);



