import React  from 'react';
import Radium from 'radium';

class Input extends React.Component {
  render() {
    const {value, onChange, placeholder, name, style} = this.props;
    return (
      <div style={[styles.inputHolder, style]}>
        <input key={name} value={value} onChange={onChange} style={styles.input} placeholder={placeholder}/>
      </div>
    );
  }
}

const styles = {
  inputHolder: {
    padding: '0',
    margin: '0 0 10px 0',
    border: '1px solid #cccccc',
    width: '49%',
    borderRadius: '3px',
    overflow: 'hidden',
    backgroundColor: '#fff',
    '@media (max-width: 600px)': {
      width: '100%',
      marginBottom: '10px'
    },
    boxSizing: 'border-box'
  },
  input: {
    padding: '5px 8px',
    width: '130%',
    height: '38px',
    border: 'none',
    boxShadow: 'none',
    backgroundColor: 'transparent',
    backgroundImage: 'none',
    appearence: 'none',
    ':focus': {
      outline: 'none'
    },
    fontSize: '14px'
  },
};

export default Radium(Input);



