import * as fs            from 'fs';
import http               from 'http';
import Express            from 'express';
import compression        from 'compression';
import React              from 'react';
import { renderToString } from 'react-dom/server';
import { StaticRouter }   from 'react-router-dom';

import entryPoint         from './server/';
import App                from './app/components/App';
import Home               from './app/pages/Home'

const app    = new Express();
const server = http.createServer(app);

const port   = process.env.PORT     || 3000;
const env    = process.env.NODE_ENV || 'production';



// const config = JSON.parse(fs.readFileSync('static/strings-en.json', 'utf8'));

const lang = 'static/strings-en.json'

const config = JSON.parse(fs.readFileSync(lang, 'utf8'));

const french = JSON.parse(fs.readFileSync('static/strings-fr.json', 'utf8'));

let script;
if (env !== 'production') {
  require('reload')(server, app); /* Enable auto-reloading */
  script = '/reload/reload.js';
}

app.use(compression());
app.use(require('body-parser').json())
app.use(Express.static('static'));

app.set('view engine', 'ejs');

app.get('/js/bundle.js', (req, res) => {
  res.sendFile('client-bundle.js', { root: process.cwd() });
});

app.get('/404', (req, res, next) => {res.status('404'); next();});
app.get('*', (req, res) => {
  let ctx = {}, markup = renderToString(
    <StaticRouter location={req.url} context={ctx}>
      <App radiumConfig={{userAgent: req.headers['user-agent']}} config={config} />
    </StaticRouter>
  );
  if (ctx.url) res.redirect(ctx.url); else res.render('index', {markup, script});
});

entryPoint(app);

server.listen(port, err => {
  if (err) return console.error(err);

  console.info(`Server running on http://localhost:${port} [${env}]`);
});



