import React      from 'react';

class Title extends React.Component {
  render() {
    return (
      <h1 {...this.props} style={styles.title}>{this.props.children}</h1>
    );
  }
}

const styles = {
  title: {
    fontSize: '21px',
    // textTransform: 'uppercase',
    marginTop: '45px'
  }
};

export default Title;



