import React         from 'react';
import Radium        from 'radium';

import {
  Nav,
  Footer,
  HeroVideo,
  HeroImage,
  Title,
  Text,
  Section,
  ImageSplit,
  ImageSplitPro,
  Features,
  People,
  DeviceLookup,
  DetailsInput,
  BuyButton,
  YouTube
}            from '../components';

class Product extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        atTop: true
    }
    this.jdata = {
        phones: [
            {
                name:'iPhone X',
                cap: '7x'
            },
            {
                name:'iPhone 7 Plus',
                cap: '5.5x'
            },
            {
                name:'Samsung S7 Edge',
                cap: '4.4x'
            }
        ],
        phones2: [
            {
                name:'Google Pixel',
                cap: '5.7x'
            },
            {
                name:'LG 5',
                cap: '5.7x'
            },
            {
                name:'iPad Pro 12.9',
                cap: '1.5x'
            }
        ],
        laptops: [
            {
                name:'MacBook Pro 15"',
                cap: '1.2x'
            },
            {
                name:'MacBook Pro 13"',
                cap: '1.5x'
            },
            {
                name:'MacBook Air 13"',
                cap: '1.5x'
            }
        ],
        laptops2: [
            {
                name:'HP EliteBook Folio',
                cap: '1.56x'
            },
            {
                name:'Asus Zenbook 3',
                cap: '1.5x'
            },
            {
                name:'Acer Aspire s13',
                cap: '1.32x'
            }
        ],
        techSpecs: [
            {
                title: "Dimensions",
                text: "W: 285mm - L: 200mm - H: 13mm",
                textLow: "Weight 321g",
                icon: "img/dim.png"
            },
            {
                title: "Output",
                text: "37w min with 37wh battery.",
                textLow: "74w min with 74wh battery.",
                icon: "img/port.png"
            }

        ],
        techSpecsMid: [
            {
                title: "Ports",
                text: "DC Output, 2x USB-C Power Delivery in/out ports (supporting 5,9,12,15, 20V output 60w max)",
                textLow: "1x USB-A QC 3.0 output (5-20v 18W max), 1x Qi Wireless 3.0 charging port (5V, 1A, 5W max)",
                icon: "img/port.png"
            },
            {
                title: "Pass through charging",
                text: "LAER supports pass through charging allowing it to charge the internal battery while charging all connected devices simultaneously",
                icon: "img/port.png"
            }
        ],
        techSpecsRight: [
            {
                title: "37wh Battery ",
                text: "2x 3.7v 5,000mAh",
                textLow: "Weight 336g",
                icon: "img/battery.png"
            },
            {
                title: "74wh Battery ",
                text: "2x 3.7v 10,000mAh.",
                textLow: " Weight 494g",
                icon: "img/battery.png"
            }
        ],
    }

  }
  componentDidMount() {
      window.addEventListener('scroll', this.handleScroll.bind(this));
  }
  componentWillUnmount() {
      window.removeEventListener('scroll', this.handleScroll.bind(this));
  }
  handleScroll(event) {
      let scrollTop = event.srcElement.body.scrollTop;
      if (scrollTop < 10 != this.state.atTop) {
        /* Only make call to setstate when value is different */
        this.setState({
          atTop: scrollTop < 10
        });
      }
  }
  render() {
    const {atTop} = this.state;
    const {config} = this.props;

    const navLinks = {
      Home: "/",
      Product:   "/product",
      about:    "/about"
    };

    return (
      <div>
        <Nav links={navLinks} buyLink={config.links.buy}/>
        <div style={styles.container}>
        </div>
        <Section>
        <Title>Features</Title>
            <div style={styles.featuresvideo}>
                <YouTube
                    videoId={config.videos.features}
                    showinfo="1"
                    controls="0"
                    modest="1"
                    rel="0"
                    showinfo="0"
                />
            </div>
            <Features sections={config.features}/>
        </Section>
        <Section style={styles.dark} scrollName={navLinks.design}>
            <Title>Charge Capacity</Title>
            <div style={styles.panelRight}>
                <div style={styles.innerPanelC}>
                    {this.jdata.phones2.map((data, idx)=>{
                        return <div><p>{data.name}</p>
                        <div style={styles.spinner}><p style={styles.spinnerText}>{data.cap}</p></div>
                        </div>
                    })}
                </div>
                <div style={styles.innerPanelC}>
                    {this.jdata.phones.map((data, idx)=>{
                        return <div><p>{data.name}</p>
                        <div style={styles.spinner}><p style={styles.spinnerText}>{data.cap}</p></div>
                        </div>
                    })}
                </div>
                <div style={styles.innerPanelC}>
                    {this.jdata.laptops2.map((data, idx)=>{
                        return <div><p>{data.name}</p>
                        <div style={styles.spinner}><p style={styles.spinnerText}>{data.cap}</p></div>
                        </div>
                    })}
                </div>
                <div style={styles.innerPanelC}>
                    {this.jdata.laptops.map((data, idx)=>{
                        return <div><p>{data.name}</p>
                        <div style={styles.spinner}><p style={styles.spinnerText}>{data.cap}</p></div>
                        </div>
                    })}
                </div>
            </div>
        </Section>
        <Section scrollName={navLinks.about}>
            <Title>Technical Specifications</Title>
            <div>
                <div style={styles.innerPanelL}>
                    <Title></Title>
                    {this.jdata.techSpecs.map((data, idx)=>{
                        return <div style={styles.techBox}>
                            <div style={styles.infoHolder}>
                                <h4 style={styles.txtTitle}>{data.title}</h4>
                                <p style={styles.smallText}>{data.text}</p><br></br>
                                <p style={styles.smallText}>{data.textLow}</p>
                            </div>
                        </div>
                    })}
                </div>
                <div style={styles.innerPanel}>
                    <Title></Title>
                    {this.jdata.techSpecsMid.map((data, idx)=>{
                        return <div style={styles.techBox}>
                            <div style={styles.infoHolder}>
                                <h4 style={styles.txtTitle}>{data.title}</h4>
                                <p style={styles.smallText}>{data.text}</p><br></br>
                                <p style={styles.smallText}>{data.textLow}</p>
                            </div>
                        </div>
                    })}
                </div>
                <div style={styles.innerPanelR}>
                    <Title></Title>
                    {this.jdata.techSpecsRight.map((data, idx)=>{
                        return <div style={styles.techBox}>
                            <div style={styles.infoHolder}>
                                <h4 style={styles.txtTitle}>{data.title}</h4>
                                <p style={styles.smallText}>{data.text}</p><br></br>
                                <p style={styles.smallText}>{data.textLow}</p>
                            </div>
                        </div>
                    })}
                </div>
            </div>
        </Section>
        <Section style={styles.dark} scrollName={navLinks.design}>
          {
            Object.keys(config.sections.product).map((title, k) => {
              let dir = k % 2 === 0 ? 'left' : 'right';
              let {text, src} = config.sections.product[title];
              return (
                <ImageSplit key={k} title={title} direction={dir} text={text} src={src} />
              )
            })
          }
        </Section>
        <Section style={styles.dark} scrollName={navLinks.support}>
          <Title>Support</Title>
          <Text>{config.sections.support}</Text>
          <DetailsInput />
          <Text style={styles.padbottom}>
            Want to contact us by email? Email us at <a style={styles.link} href="mailto:support@arroe.co">support@arroe.co</a>
          </Text>
          <br />
        </Section>
        <Section scrollName="notify">
          <Title>Get Notified</Title>
          <Text>Enter your name and email below and well let you know when were launching</Text>
          <DetailsInput signup={true} />
        </Section>
        <Footer links={config.media} />
        <div style={[styles.buybanner, {display: atTop ? 'none' : 'block'}]}>
          <BuyButton link={config.links.buy} />
        </div>
      </div>
    );
  }
}

const styles = {
  features: {
    backgroundImage: 'url(img/hero-image.jpg)',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    height: '100vh'
  },
  playbutton: {
    width: '100px'
  },
  dark: {
    backgroundColor: '#1e1e1e',
    color: 'white'
  },
  featuresvideo: {
    width: '760px',
    height: '400px',
    margin: '20px auto 40px',
    maxWidth: '80%'
  },
  featuresvideo: {
    width: '760px',
    height: '400px',
    margin: '20px auto 40px',
    maxWidth: '100%'
  },
  buybanner: {
    position: 'fixed',
    bottom: '0',
    width: '100%',
    backgroundColor: '#1e1e1e',
    textAlign: 'center',
    color: 'white',
    '@media (min-width: 601px)': {
      display: 'none'
    },
    padding: '10px',
    boxSizing: 'border-box'
  },
  link: {
    color: 'white'
  },
  justify: {
    textAlign: 'justify'
  },
  container: {
    height: '100vh',
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
    position: 'relative',
    zIndex: 0,
    backgroundImage: 'url(img/product-hero.JPG)',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    paddingTop: '80px',
    boxSizing: 'border-box'
  },
  wrapper: {
    margin: '0 auto',
    position: 'relative'
  },
  panelLeft: {
    order: 1,
    width: '50vh',
    maxWidth: '50vh',
    backgroundColor: 'black',
    float: 'left',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  panelRight: {
    order: 2,
    width: '100%',
    float: 'right',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  innerPanelC: {
    "width" : "25%",
    float: 'right',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  innerPanel: {
    float: 'left',
    width: '33%',
    '@media (max-width: 600px)': {
      display: 'block',
      marginBottom: '-4px',
      width: '100%',
      height: '20%'
    }
  },
  innerPanelL: {
    float: 'left',
    width: '33%',
    '@media (max-width: 600px)': {
      display: 'block',
      marginBottom: '-4px',
      width: '100%',
    }
  },
  innerPanelR: {
    float: 'right',
    width: '33%',
    '@media (max-width: 600px)': {
      display: 'block',
      marginBottom: '-4px',
      width: '100%',
    }
  },
  "spinner": {
    "width": "50px",
    "height": "50px",
    "borderRadius": "50%",
    "border": "4px solid white",
    "margin": "10px",
    "display": "inline-block"
  },
  "spinner2": {
    "float": "left",
    "width": "50px",
    "height": "50px",
    "background": "transparent",
    "borderRadius": "50%",
    "border": "4px solid #1e1e1e",
    "marginLeft": "100px",
    "display": "inline-block",
    '@media (max-width: 600px)': {
        "width": "30px",
        "height": "30px",
        display: 'block',
    }
  },
  "infoHolder": {
    "textAlign": "center",
    "paddingLeft": "15px",
    "float": "left",
    "display": "inline-block",
    "width" : "100%",
    '@media (max-width: 600px)': {
        width : "90%",
        marginTop : '-5px',
        display: 'block'
    }
  },
  "techBox": {
    "width": "100%",
    height: "23vh",
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }

  },
  "txtTitle": {
    textTransform: 'uppercase',
  },
  "spinnerText": {
    "position": "relative",
    "top": "-3px"
  },
  "imageHolderInner":{
    "height": "90vh",
    "width" : "90%",
    '@media (max-width: 600px)': {
      "height" : "50vh",
      "width" : "100%"
    }

  },
  "imageInner":{
    "paddingTop": "30%",
    "verticalAlign" : "middle",
    "width" : "100%",
    "margin": "auto"
  },
  "smallText": {
    "marginTop": "-20px",
    "fontSize" : "12px"
  }
}

export default Radium(Product);



