import React         from 'react';
import Radium        from 'radium';

import Scrollchor    from 'react-scrollchor';
import YouTube       from './YouTube';
import Title         from './Title';

class HeroVideo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
  }
  render() {
    const {preview} = this.props;
    const {show} = this.state;

    return (
      <div style={[styles.container, {backgroundImage: `url(${preview})`}]}>
        { !show ?
          <div>
            <div style={styles.gradient} >
            </div>
            <img src="img/play-button.svg" style={styles.button} onClick={() => this.setState({show: true})}/>
          </div>
          :
          <YouTube
            videoId={this.props.videoId}
            autoplay="1"
            rel="0"
            modest="1"
            controls="0"
            showinfo="0"
          />
        }
      </div>
    );
  }
}

const styles = {
  container: {
    height: '100vh',
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
    position: 'relative',
    zIndex: 0,
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    paddingTop: '80px',
    boxSizing: 'border-box'
  },
  gradient: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundImage: 'linear-gradient(to bottom, rgba(30, 30, 30, 0), #1e1e1e)',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-end',
    textAlign: 'center',
    alignItems: 'center'
  },
  button: {
    position: 'relative',
    width: '100px',
    cursor: 'pointer'
  },
  scrolldown: {
    width: '30px',
    marginBottom: '20px'
  },  
  quote: {
    color: 'white',
  },
  qLogo: {
    
  }
}

export default Radium(HeroVideo);



