import React  from 'react';
import Radium from 'radium';

class ImageSplitPhone extends React.Component {
  render() {
    var {direction, src} = this.props;

    direction = direction === 'right' ? 'right' : 'left';

    return (
      <div style={styles.container}>
        <div style={[styles.panel, direction === 'left' ? styles.left : styles.right]}>
          <img src={src} style={styles.img}/>
        </div>
        <div style={[styles.panel, direction === 'left' ? styles.right : styles.left]}>
          <div style={styles.textBox}>
            <p style={styles.title}>{this.props.title}</p>
            <p style={styles.content}>{this.props.text}</p>
          </div>
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    width: '100%',
    display: 'flex',
    '@media (max-width: 600px)': {
      display: 'block'
    }
  },
  left: {
    order: 1,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end'
  },
  right: {
    order: 2,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start'
  },
  panel: {
    width: '50%',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
    }
  },
  img: {
    alignSelf: 'center',
    height: '520px',
    '@media (max-width: 600px)': {
      display: 'block',
      width: '100%',
      height: 'auto'
    }
  },
  textBox: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    textAlign: 'left',
    width: '100%',
    boxSizing: 'border-box',
    padding: '40px 20%',
    '@media (max-width: 600px)': {
      padding: '40px 20px'
    }
  },
  title: {
    fontSize: '20px',
    // textTransform: 'uppercase',
    margin: '0px',
    fontWeight: 'bold'
  },
  content: {
    fontSize: '14px'
  }
};

export default Radium(ImageSplitPhone);



