import React  from 'react';
import Radium from 'radium';
class Text extends React.Component {
  render() {
    return (
      <p style={[styles.text, this.props.style]}>{this.props.children}</p>
    );
  }
}

const styles = {
  text: {
    fontSize: '16px',
    maxWidth: '760px',
    margin: 'auto',
    padding: '20px'
  }
};

export default Radium(Text);



