import React      from 'react';
import Scrollchor from 'react-scrollchor';
import Radium     from 'radium';
import {Link}     from 'react-router-dom';


import BuyButton  from './BuyButton';

class NavLight extends React.Component {
  render() {
    const {links} = this.props;
    return (
      <div style={[styles.container, {position: 'fixed'}]}>
        <div style={styles.inner}>
          <Scrollchor style={styles.logoContainer} to="#">
            <img style={styles.logo} src="img/arroe-logo-dark.svg" />
          </Scrollchor>
          <div style={styles.langHolder}>
              <Link to='/purchase'>
                <img style={styles.flags} src="img/eng.png" />
              </Link>
              <Link to='/purchase_fr'>
                <img style={styles.flags} src="img/fr.png" />
              </Link>
              <Link to='/purchase_gr'>
                <img style={styles.flags} src="img/ger.png" />
              </Link>
            </div>
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    backgroundColor: 'white',
    color: 'black',
    height: '80px',
    width: '100%',
    zIndex: 1000,
    position: 'fixed'
  },
  inner: {
    display: 'flex',
    height: '100%',
    flexDirection: 'row',
    justifyContent: 'center',
    margin: 'auto',
    maxWidth: '1000px',
    padding: '0px 20px'
  },
  logoContainer: {
    width: '111px',
    flexGrow: 0,
    display: 'flex',
    justifyContent: 'center'
  },
  linkContainer: {
    flexGrow: 1,
    display: 'flex',
    justifyContent: 'center',

  },
  orderBtnContainer: {
    color: 'White',
    width: '120px',
    flexGrow: 0,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    '@media (max-width: 600px)': {
      display: 'none'
    },
    fontSize: 16,
    textAlign: 'right'
  },
  logo: {
    width: '100%',
    fill: 'black'
  },
  link: {
    color: 'black',
    textTransform: 'uppercase',
    display: 'flex',
    textAlign: 'center',
    flexDirection: 'column',
    justifyContent: 'center',
    margin: '0px 10px',
    textDecoration: 'none',
    fontSize: 14,
    fontWeight: 600,
    letterSpacing: '1.75px'
  },
  buy: {
    color: 'white'
  },
  langHolder:{
    display: 'flex',
    height: '100%',
    flexDirection: 'row',
    justifyContent: 'center',
    margin: 'auto',
    maxWidth: '1000px',
    padding: '0px 20px',
    position: 'absolute',
    right: '5%',
    justifyContent: 'center',
    '@media (max-width: 600px)': {
      position: 'right',
      right: '10px',
      padding: '5px'
    },
  },
  flags: {
    display: 'flex',
    borderRadius: '180',
    width: '30px',
    height: '30px',
    padding: '5px',
    margin: 'auto',
    marginTop: '20px',
    verticalAlign: 'baseline',
    '@media (max-width: 600px)': {
      width: '20px',
      height: '20px',
    },
  }
};

export default Radium(NavLight);



