/* Entry point for api routes with express */
import { MailChimpAPI } from 'mailchimp';

let api;
try {
  api = new MailChimpAPI('c266287611382a5f62a532c84ac35cf8-us15', { version : '2.0' });
} catch (e) {
  console.log('Could not initialise MailChimp API');
}

var entryPoint = (app) => {
  app.post('/api/subscribe', (req, res) => {
    api.call('lists', 'subscribe', {
      id: 'abfd3d6379',
      email: {
        email: req.body["Email"]
      },
      "merge_vars": {
        FNAME: req.body["First Name"],
        LNAME: req.body["Last Name"]
      },
      double_optin: false,
    }, (err, data) => {
      if (err) {
        res.json({status: 'failed', err});
      } else {
        res.json({status: 'succeeded'});
      }
    });
  })
}

export default entryPoint;



