import React         from 'react';
import Radium        from 'radium';

import {
  Nav,
  NavLight,
  Footer,
  HeroVideo,
  Title,
  Text,
  Section,
  ImageSplit,
  Features,
  People,
  DeviceLookup,
  DetailsInput,
  BuyButton,
  YouTube,
  InTheBox_gr,
  TechSpecs_gr,
  ImageSplitPhone
}            from '../components';

class Purchase_gr extends React.Component {
  constructor(props) {
    super(props);
    showTech: false;
    isTechActive: false;
  }
  componentDidMount() {
      window.addEventListener('scroll', this.handleScroll.bind(this));
  }
  componentWillUnmount() {
      window.removeEventListener('scroll', this.handleScroll.bind(this));
  }
  handleScroll(event) {
      let scrollTop = event.srcElement.body.scrollTop;
      if (scrollTop < 10 != this.state.atTop) {
        /* Only make call to setstate when value is different */
        this.setState({
          atTop: scrollTop < 10,

        });
      }
  }
  renderTechSpecs(){
      this.setState({
          showTech: true,
          isTechActive: true
      })
  }

  renderBox(){
      this.setState({
          showTech: false,
          isTechActive: false
      })
  }

  render() {
    const {atTop} = this.state;
    const {config} = this.props;

    const navLinks = {
      Home: "/",
      Product:   "/product",
      about:    "/about"
    };

    return (
      <div>
          {/* comment */}
        <NavLight links={navLinks} buyLink={config.links.buy}/>
        <HeroVideo
          style={styles.section}
          preview="img/21.png"
          videoId={config.videos.unbox}
          downLink={'#' + navLinks.features}
        />
        <Section style={styles.light} >
            <Title>Ist dein Laptop-Adapter in der Verpackung?</Title>
            <Text>
            LAER beinhaltet Adapter fr die meisten Laptop Marken und Modelle.
            </Text>
            <Text>
            Bitte besttige mit Hilfe des folgenden Formulars, dass dein Laptop-Adapter in der Verpackung enthalten ist. Ist dies nicht der Fall, so kannst du auf unserer Website unter www.arroe.co einen passenden Adapter fr deinen Laptop bestellen.</Text>
            <DeviceLookup devices={config.devices} />
          <br />
        </Section>
        <Section style={styles.light} >
          {
            Object.keys(config.sections.purchase_gr).map((title, k) => {
              let dir = k % 2 === 0 ? 'right' : 'left';
              let {text, src} = config.sections.purchase_gr[title];
              return (
                <ImageSplit key={k} title={title} direction={dir} text={text} src={src} />
              )
            })
          }
        </Section>
        <Section style={styles.dark} >
        <Title>Mach dir nie wieder Sorgen um deinen Akku.</Title>

          {
            Object.keys(config.sections.appSection_gr).map((i, k) => {
              let {text, src} = config.sections.appSection_gr[i];
              return (
                <ImageSplitPhone key={k}  direction='left' text={text} src={src} />
              )
            })
          }
        </Section>

        <Section style={styles.light} >
          {
            Object.keys(config.sections.insidebox_gr).map((i, k) => {
              let dir = k % 2 === 0 ? 'left' : 'right';
              let {text, src} = config.sections.insidebox_gr[i];
              return (
                <ImageSplit key={k}  direction={dir} text={text} src={src} />
              )
            })
          }
        </Section>
        <Section>
            <a style={this.state.isTechActive ? styles.inactive : styles.active} onClick={()=>this.renderBox()}>In der Verpackung</a>
            <a style={this.state.isTechActive ? styles.active : styles.inactive} onClick={()=>this.renderTechSpecs()}> Einzelheiten</a>
        </Section>
        <Section style={styles.light} >
          {
              this.state.showTech?
                <TechSpecs_gr />
                :
              <div>
                <InTheBox_gr  src={config.techImg.src}/>
                
              </div>
          }
        </Section>
        <Footer links={config.media} />
      </div>
    );
  }
}

const styles = {
  features: {
    backgroundImage: 'url(img/hero-image.jpg)',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    height: '100vh'
  },
  playbutton: {
    width: '100px'
  },
  light: {
    backgroundColor: 'white',
    color: 'black'
  },
  dark: {
    backgroundColor: '#0d121b',
    color: 'white'
  },
  featuresvideo: {
    width: '760px',
    height: '400px',
    margin: '20px auto 40px',
    maxWidth: '100%'
  },
  buybanner: {
    position: 'fixed',
    bottom: '0',
    width: '100%',
    backgroundColor: '#1e1e1e',
    textAlign: 'center',
    color: 'white',
    '@media (min-width: 601px)': {
      display: 'none'
    },
    padding: '10px',
    boxSizing: 'border-box'
  },
  link: {
    color: 'white'
  },
  justify: {
    textAlign: 'justify'
  },
  active: {
      paddingLeft: '20px',
      paddingRight: '20px',
      fontWeight: 'bold',
      textDecorationLine: 'underline'
  },
  inactive: {
      paddingLeft: '20px',
      paddingRight: '20px',
      textDecorationLine: 'none'
  }
}

export default Radium(Purchase_gr);



