import React  from 'react';
import Radium from 'radium';
class Section extends React.Component {
  render() {
    const {scrollName} = this.props
    return (
      <div style={[styles.container, this.props.style]}>
        <div id={scrollName} style={styles.scrollTarget} />
        {this.props.children}
      </div>
    );
  }
}

const styles = {
  container: {
    textAlign: 'center',
    overflow: 'hidden',
    paddingBottom: '55px'
  },
  text: {
    fontSize: '16px',
    margin: 'auto'
  },
  scrollTarget: {
    marginTop: '-55px',
    paddingTop: '55px'  
  }
};

export default Radium(Section);



