import React        from 'react';
import Radium       from 'radium';
import DetailsInput from './DetailsInput';
import Text         from './Text';
import BuyButton    from './BuyButton';

class DeviceLookup extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      brand: '',
      device: '',
      model: ''
    }
  }
  brandChange(event) {
    this.setState({brand: event.target.value, device: ''});
  }
  deviceChange(event) {
    this.setState({device: event.target.value, model: ''});
  }
  modelChange(event) {
    this.setState({model: event.target.value});
  }
  renderContact() {
    return (
      <div>
        <Text>Wed like to find out more about your device so you can get the product that fits best, please get in touch!</Text>
        <DetailsInput brand={this.state.brand} device={this.state.device} />
      </div>
    );
  }
  renderCompatible() {
    return (
      <div>
        <p><img src="img/tick-icon.svg" style={styles.tick}/>Yes! The connectors are included in the box</p>
      </div>
    );
  }
  render() {
    const {devices} = this.props;
    const {brand, device, model} = this.state;

    // console.log(device)
    return (
      <div style={styles.container}>
        <div style={styles.selects}>
          <div style={styles.selectHolder}>
            <select style={[styles.select, brand === '' ? styles.inactive : '']} value={brand} onChange={this.brandChange.bind(this)} key="brand">
              <option disabled value="">Select Brand</option>
              {
                devices ? Object.keys(devices).map(brand => (
                  <option key={brand} value={brand}>{brand}</option>
                )) : null
              }
            </select>
          </div>
          <div style={styles.selectHolder}>
            <select style={[styles.select, device === '' ? styles.inactive : '']} value={device} onChange={this.deviceChange.bind(this)} key="device">
              <option disabled value="">Select Range</option>
              {
                !!devices && devices[brand] ? devices[brand].map(device => (
                  <option key={device.id} value={device.name}>{device.name}</option>
                )) : null
              }
              {/* {
                !!devices && devices[brand] ? devices[brand].map(device => (
                  <option key={device} value={device}>{device}</option>
                )) : null
              } */}
            </select>
          </div>
          <div style={styles.selectHolder}>
            <select style={[styles.select, model === '' ? styles.inactive : '']} value={model} onChange={this.modelChange.bind(this)} key="model">
              <option disabled value="">Select Model</option>
              {
                !!devices && devices[brand] ? devices[brand].map(model => (
                  model.name == device ? model.models.map(types =>(
                    <option key={types} value={types}>{types}</option>
                  )) : null
                  // <option key={model.id} value={model.models}>{model.models}</option>
                )) : null
              }
            </select>
          </div>
        </div>
        {
          brand === 'Other' || device === 'Other' || model == 'Other' ? this.renderContact() :
            (device !== '' && model !== '' ? this.renderCompatible() : null)
        }
      </div>
    );
  }
}

const styles = {
  container: {
    maxWidth: '500px',
    margin: 'auto',
    padding: '0px 20px'
  },
  selects: {
    display: 'flex',
    justifyContent: 'space-between',
    width: '100%',
    '@media (max-width: 600px)': {
      flexDirection: 'column'
    }
  },
  tick: {
    height: '15px',
    marginRight: '10px'
  },
  buy: {
    color: 'white'
  },
  selectHolder: {
    padding: '10px 0px 0px 0px',
    margin: '0',
    border: '1px solid #ccc',
    width: '49%',
    height: '38px',
    borderRadius: '3px',
    overflow: 'hidden',
    backgroundColor: '#fff',
    '@media (max-width: 600px)': {
      width: '100%',
      marginBottom: '10px'
    },
    backgroundImage: 'url(img/down-arrow.svg?)',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '98% 50%',
    backgroundSize: '10px'
  },
  select: {
    padding: '5px 8px',
    width: '130%',
    border: 'none',
    boxShadow: 'none',
    backgroundColor: 'transparent',
    backgroundImage: 'none',
    appearence: 'none',
    ':focus': {
      outline: 'none'
    },
    fontSize: '14px'
  },
  inactive: {
    color: '#cccccc'
  }
};

export default Radium(DeviceLookup);



