import React  from 'react';
import Radium from 'radium';

import axios  from 'axios';
import {Link} from 'react-router-dom';

import Text   from './Text';
import Input  from './Input';

class DetailsInput extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      first: '',
      last: '',
      email: '',
      mode: 'init',
      message: '',
      device: ''
    }
  }
  firstChange(event) {
    this.setState({first: event.target.value});
  }
  lastChange(event) {
    this.setState({last: event.target.value});
  }
  emailChange(event) {
    this.setState({email: event.target.value});
  }
  deviceChange(event) {
    this.setState({device: event.target.value})
  }
  handleSubmit(event) {
    event.preventDefault();

    if (this.props.signup) {
      const {first, last, email} = this.state;

      axios.post('api/subscribe', {
        "First Name": first,
        "Last Name" : last,
        "Email":      email
      }).then((res) => {
        const status = res.data.status;
        console.log(res.data);
        if (status==="succeeded") {
          this.setState({mode: 'success', message: 'Thanks for signing up! We\'ll be in touch soon!'})
        } else {
          let message;

          if (!!res.data.err && !!res.data.err.code) {
            switch (res.data.err.code) {
              case 214:
                message = "You're already on our list!";
                break;
              case -100:
                message = "You didn't enter a valid email address";
                break;
              default:
                message = `Something went wrong - error code ${res.data.err.code}`
            }
          } else {
            message = "An unknown error occurred"
          }
          this.setState({
            mode: 'error',
            message
          });
        }
      });
    } else {
      const {first, last, email, device} = this.state;

      axios.post('https://formspree.io/support@arroe.co', {
        "First Name": first,
        "Last Name" : last,
        "Email":      email,
        "Device":     device,
        "_replyto":   email
      }).then((res) => {
        this.setState({mode: 'success', message: 'Thanks for your message, we\'ll respond soon'})
      }).catch((err) => {
        this.setState({mode: 'error', message: 'Something went wrong submitting your form'})
      });
    }
  }
  renderInit() {
    const {devices, signup} = this.props;
    return (
      <form style={styles.container}>
        <div style={styles.nameHolder}>
          <Input name="First Name" placeholder="First Name" style={[styles.name, styles.input]} value={this.state.first} onChange={this.firstChange.bind(this)} />
          <Input name="Last Name" placeholder="Last Name" style={[styles.name, styles.input]} value={this.state.last} onChange={this.lastChange.bind(this)} />
        </div>
        <Input name="Email" placeholder="Email Address" style={[styles.email, styles.input]} value={this.state.email} onChange={this.emailChange.bind(this)} />
        {
          signup ? (
            <div>
              <a style={styles.link} href="#" onClick={this.handleSubmit.bind(this)}>
                <div style={styles.box}>Sign Up</div>
              </a>
              <p style={styles.small}>By signing up you agree to our <Link to="/privacy">Privacy Policy.</Link></p>
            </div>
          ) : (
            <div>
              <Input name="user-device" style={[styles.email, styles.input]} placeholder="Enter your device information" value={this.state.device} onChange={this.deviceChange.bind(this)} />
              <a style={styles.link} href="#" onClick={this.handleSubmit.bind(this)}>
                <div style={[styles.box, styles.light]}>Send</div>
              </a>
            </div>
          )
        }
      </form>
    );
  }
  render() {
    switch (this.state.mode) {
      case 'init':
        return this.renderInit();
        break;
      case 'error':
        return (
          <div style={styles.container}>
            <p>{this.state.message}</p>
            <a href="#" onClick={(e) => {e.preventDefault(); this.setState({mode: 'init'})}}> Try again </a>
          </div>
        )
      case 'success':
        return (<div style={styles.container}>
          <img src="img/tick-icon.svg" style={styles.success}/>
          <h3>{this.state.message}</h3>
        </div>)
    }
  }
}

const styles = {
  container: {
    maxWidth: '500px',
    margin: '0px auto',
    minHeight: '150px',
    padding: '0px'
  },
  name: {
    width: '49%',
  },
  input: {
    '@media (max-width: 600px)': {
      width: '100%'
    }
  },
  tick: {
    height: '15px',
    marginRight: '10px'
  },
  email: {
    width: '100%'
  },
  success: {
    width: '60px'
  },
  nameHolder: {
    display: 'flex',
    justifyContent: 'space-between',
    '@media (max-width: 600px)': {
      flexDirection: 'column'
    }
  },
  small: {
    fontSize: '12px'
  },
  box: {
    backgroundColor: '#1e1e1e',
    borderRadius: '5px',
    textAlign: 'center',
    width: '120px',
    margin: 'auto',
    color: 'white',
    textTransform: 'uppercase'
  },
  light: {
    color: '#1e1e1e',
    backgroundColor: '#ffffff'
  },
  link: {
    textDecoration: 'none'
  }
};

export default Radium(DetailsInput);



